.navbarItems{
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0,0,0, .25);
    width: 95%;
    height:80px;
    border-radius: 13px;
    cursor: pointer;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
}
.logo{
    text-decoration: none;
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}
.navLink{
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
   padding: 0.7rem 1rem;
   white-space: nowrap;
}
.navLink:hover{
    /* background-color: #239fcc; */
    color: #239fcc;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}
.cartCount{
    padding: 0.7rem 1rem;
}
@media (min-width: 600px) {
    .navbarItems{
    
        
        padding: 0 20px;
      
    }
  }